import { inject, Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { SellerType, StorageType } from '@core/enums';
import { StorageService } from './storage.service';

// NOTE: We named as `Common` as we're already have PlaceAdService in Place Ad module.
@Injectable({
	providedIn: 'root',
})
export class CommonPlaceAdService {
	private storageService = inject(StorageService);
	private router = inject(Router);

	gotoPlaceAd(): void {
		// TODO: Add here the amplitude tracker for Place Ad button click.

		const user = this.storageService.getItem(StorageType.USER);
		const isLoggedIn = !!user;

		// User is not logged in
		if (!isLoggedIn) {
			this.router.navigate([`/${APP_ROUTE.placeAdCategory}`], {
				queryParams: { sellerTypeId: SellerType.INDIVIDUAL },
			});
			return;
		}

		const sellerTypeId = +user.hasBusinessProfile ? SellerType.BUSINESS : SellerType.INDIVIDUAL;
		this.router.navigate([`/${APP_ROUTE.placeAdCategory}`], { queryParams: { sellerTypeId } });
	}
}
